<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in products" :key="item.product.id">
        <h3>{{ item.product.title }}</h3>
        <b>{{ totalItemPrice(item.price, item.quantity) }} ₽</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>{{ currentDelivery }}</b></p>
      <p>Оплата: <b>{{ currentPay }}</b></p>
      <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ orderPrice }} ₽</b></p>
    </div>

    <button v-if="this.$route.name === 'order'"
            class="cart__button button button--primery"
            type="submit">
      Оформить заказ
    </button>
  </div>
</template>

<script>
export default {
  name: 'OrderOptions',
  props: {
    products: {},
    totalPrice: {},
    delivery: {
      default: 0,
    },
    pay: {
      default: 'Оплата картой при получении',
    },
  },
  methods: {
    totalItemPrice(price, count) {
      return price * count;
    },
  },
  computed: {
    currentDelivery() {
      if (this.delivery === 0) {
        return 'бесплатно';
      }

      return `${this.delivery} ₽`;
    },

    currentPay() {
      switch (this.pay) {
        case 'card':
          return 'Картой при получении';
        case 'cash':
          return 'Наличными при получении';
        default:
          return 'Картой при получении';
      }
    },

    orderPrice() {
      return this.totalPrice + this.delivery;
    },
  },
};
</script>
