<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form"
            action="#"
            method="POST"
            @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text-input title="ФИО"
                                  placeholder="Введите ваше полное имя"
                                  type="text"
                                  :error="formError.name"
                                  v-model:value="formData.name"/>

            <base-form-text-input title="Адрес доставки"
                                  placeholder="Введите ваш адрес"
                                  type="text"
                                  :error="formError.address"
                                  v-model:value="formData.address"/>

            <base-form-text-input title="Телефон"
                                  placeholder="Введите ваш телефон"
                                  type="tel"
                                  :error="formError.phone"
                                  v-model:value="formData.phone"/>

            <base-form-text-input title="Email"
                                  placeholder="Введите ваш Email"
                                  type="email"
                                  :error="formError.email"
                                  v-model:value="formData.email"/>

            <base-form-text-area title="Комментарий к заказу"
                                 :error="formError.comment"
                                 v-model:value="formData.comment"
                                 placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label for="delivery"
                       class="options__label"
                       @click="selectDeliveryOptions(0)">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="delivery"
                         value="0"
                         :checked="this.delivery === 0"
                         v-model="delivery">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label for="delivery"
                       class="options__label"
                       @click="selectDeliveryOptions(500)">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="delivery"
                         value="500"
                         :checked="this.delivery === 500"
                         v-model="delivery">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label for="pay" class="options__label" @click="selectPayOptions('card')">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    :checked="this.pay === 'card'"
                    v-model="pay"
                    value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label for="pay" class="options__label" @click="selectPayOptions('cash')">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="pay"
                         :checked="this.pay === 'cash'"
                         v-model="pay"
                         value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <order-options :products="products"
                       :total-price="totalPrice"
                       :delivery="delivery"
                       :pay="pay"/>
        <div class="cart__error form__error-block" v-if="formErrorGlobal">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorGlobal }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormTextInput from '@/components/BaseFormTextInput/BaseFormTextInput.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea/BaseFormTextArea.vue';
import OrderOptions from '@/components/OrderOptions/OrderOptions.vue';
import { mapMutations, mapGetters } from 'vuex';
import axios from 'axios';
import { BASE_URL } from '@/api/api.config';

export default {
  name: 'OrderPage',
  components: {
    OrderOptions,
    BaseFormTextInput,
    BaseFormTextArea,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorGlobal: '',
      delivery: 0,
      pay: 'card',
    };
  },
  methods: {
    ...mapMutations({
      resetCart: 'resetCart',
      updateOrderInfo: 'updateOrderInfo',
    }),

    order() {
      this.formError = {};
      this.formErrorGlobal = '';
      this.delivery = 0;
      this.pay = 'card';

      axios.post(`${BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((res) => {
          this.resetCart();
          this.updateOrderInfo(res.data);
          this.$router.push({ name: 'createdOrder', params: { id: res.data.id } });
        })
        // eslint-disable-next-line no-return-assign
        .catch((err) => {
          this.formErrorGlobal = err.response.data.error.message || '';
          this.formError = err.response.data.error.request || {};
        });
    },

    selectDeliveryOptions(value) {
      this.delivery = value;
    },

    selectPayOptions(value) {
      this.pay = value;
    },
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
  },
};
</script>
