<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts }} товар
      </span>
    </div>

    <div class="content__catalog">
      <product-filter
        v-model:price-from="priceFrom"
        v-model:price-to="priceTo"
        v-model:color-id="colorId"
        v-model:category-slug="categorySlug"/>
      <section class="catalog">
        <product-list :products="paginatedProducts"/>
        <base-pagination
          v-if="countProducts > countOnPage"
          v-model:currentPage.number="page"
          :count="countProducts"
          :count-on-page="countOnPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList/ProductList.vue';
import BasePagination from '@/components/BasePagination/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter/ProductFilter.vue';
// eslint-disable-next-line import/no-extraneous-dependencies
import axios from 'axios';
import { BASE_URL } from '@/api/api.config';

export default {
  components: {
    ProductFilter,
    BasePagination,
    ProductList,
  },
  data() {
    return {
      priceFrom: 0,
      priceTo: 0,
      categorySlug: '',
      colorId: 0,
      page: 1,
      countOnPage: 6,
      productsData: null,
    };
  },

  computed: {
    paginatedProducts() {
      return this.productsData ? this.productsData.items : [];
    },

    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  methods: {
    loadProducts() {
      axios.get(`${BASE_URL}/api/products`, {
        params: {
          page: this.page,
          limit: this.countOnPage,
          categoryId: this.categorySlug,
          minPrice: this.priceFrom,
          maxPrice: this.priceTo,
          colorId: this.colorId,
        },
      })
        // eslint-disable-next-line no-return-assign
        .then((res) => this.productsData = res.data);
    },
  },

  watch: {
    page() {
      this.loadProducts();
    },
    priceTo() {
      this.loadProducts();
    },
    priceFrom() {
      this.loadProducts();
    },
    categorySlug() {
      this.loadProducts();
    },
    colorId() {
      this.loadProducts();
    },
  },

  created() {
    this.loadProducts();
    const windowData = Object.fromEntries(
      new URL(window.location).searchParams.entries(),
    );

    const VALID_KEYS = ['page', 'priceFrom', 'priceTo', 'categorySlug', 'colorId'];

    VALID_KEYS.forEach((key) => {
      if (windowData[key]) {
        this[key] = windowData[key];
      }
    });
  },
};
</script>
