import { createStore } from 'vuex';
import axios from 'axios';
import { BASE_URL } from '@/api/api.config';

export default createStore({
  state: {
    cartItems: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProductsData;
    },
    orderInfo(state) {
      return state.orderInfo;
    },
    cartTotalPrice(state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((acc, item) => (item.price * item.quantity) + acc, 0);
    },
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },

    resetCart(state) {
      state.cartProductsData = [];
    },

    updateCartProductCount(state, {
      productId,
      quantity,
    }) {
      const item = state.cartProductsData.find((p) => p.product.id === productId);

      if (item) {
        item.quantity = quantity;
      }
    },
    updateUserAccessKey(state, userAccessKey) {
      state.userAccessKey = userAccessKey;
    },
    updateCartProductsItem(state, items) {
      state.cartProductsData = items;
    },
  },
  actions: {
    loadCart(context) {
      return axios.get(`${BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        // eslint-disable-next-line no-return-assign
        .then((res) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', res.data.user.accessKey);
            context.commit('updateUserAccessKey', res.data.user.accessKey);
          }

          context.commit('updateCartProductsItem', res.data.items);
        });
    },

    addProductToCart(context, {
      productId,
      quantity,
    }) {
      return axios.post(`${BASE_URL}/api/baskets/products`, {
        productId,
        quantity,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((res) => context.commit('updateCartProductsItem', res.data.items));
    },

    updateCartProductCount(context, {
      productId,
      quantity,
    }) {
      context.commit('updateCartProductCount', {
        productId,
        quantity,
      });

      if (quantity < 1) {
        return;
      }

      // eslint-disable-next-line consistent-return
      return axios.put(`${BASE_URL}/api/baskets/products`, {
        productId,
        quantity,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((res) => context.commit('updateCartProductsItem', res.data.items));
    },

    deleteCartItem(context, productId) {
      return axios.delete(`${BASE_URL}/api/baskets/products`, {
        data: {
          productId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((res) => context.commit('updateCartProductsItem', res.data.items));
    },

    loadOrderById(context, orderId) {
      axios.get(`${BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      }).then((res) => {
        context.commit('updateOrderInfo', res.data);
      });
    },
  },
  modules: {},
});
