// eslint-disable-next-line import/no-extraneous-dependencies
import { createRouter, createWebHistory } from 'vue-router';
import MainPage from '@/pages/MainPage/MainPage.vue';
import ProductPage from '@/pages/ProductPage/ProductPage.vue';
import NotFoundPage from '@/pages/NotFoundPage/NotFoundPage.vue';
import CartPage from '@/pages/CartPage/CartPage.vue';
import OrderPage from '@/pages/OrderPage/OrderPage.vue';
import CreatedOrder from '@/pages/CreatedOrder/CreatedOrder.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: MainPage,
  },
  {
    path: '/:catchAll(.*)',
    name: 'notFoundPage',
    component: NotFoundPage,
  },
  {
    path: '/product/:id',
    name: 'product',
    component: ProductPage,
  },
  {
    path: '/cart',
    name: 'cart',
    component: CartPage,
  },
  {
    path: '/order',
    name: 'order',
    component: OrderPage,
  },
  {
    path: '/order/:id',
    name: 'createdOrder',
    component: CreatedOrder,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
