<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'home'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'home', query: { categorySlug: `${category.slug}` } }"
                       class="breadcrumbs__link">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <ProductItem v-if="productData" :product="product"/>
  </main>
</template>

<script>
import axios from 'axios';
import ProductItem from '@/components/ProductItem/ProductItem.vue';
import { BASE_URL } from '@/api/api.config';

export default {
  name: 'ProductPage',
  components: { ProductItem },
  data() {
    return {
      productData: null,
    };
  },
  computed: {
    product() {
      return this.productData ? this.productData : [];
    },

    category() {
      return this.productData ? this.productData.category : [];
    },
  },

  methods: {
    loadProductById() {
      axios.get(`${BASE_URL}/api/products/${this.$route.params.id}`)
        // eslint-disable-next-line no-return-assign
        .then((res) => this.productData = res.data);
    },
  },

  created() {
    this.loadProductById();
  },
};
</script>
