<template>
  <fieldset class="form__block">
    <legend class="form__legend">Цвет</legend>
    <ul class="colors">
      <li class="colors__item"
          v-for="color in colors"
          :key="color.id">
        <label for="colors" class="colors__label" @click="selectColor(color.id)">
          <input class="colors__radio sr-only"
                 type="radio"
                 name="color"
                 :checked="currentColorId === color.id"
                 v-model.number="color.id">
          <span class="colors__value" :style="{ backgroundColor: color.code, }">
                  </span>
        </label>
      </li>
    </ul>
  </fieldset>
</template>

<script>
export default {
  name: 'ColorsList',
  props: ['colors', 'colorId'],
  data() {
    return {
      currentColorId: this.colorId,
    };
  },
  watch: {
    colorId(value) {
      this.currentColorId = value;
    },
  },
  methods: {
    selectColor(colorId) {
      this.$emit('update:colorId', colorId);
    },
  },
};
</script>
