<script>
import ColorsList from '@/components/ColorsList/ColorsList.vue';
import { mapActions } from 'vuex';

export default {
  name: 'ProductItem',
  components: { ColorsList },
  props: ['product'],
  data() {
    return {
      currentCount: 1,
      currentColorId: this.product.colors[0].id,
      currentProduct: null,
      productAdded: false,
      productAddSending: false,
    };
  },

  methods: {
    ...mapActions(['addProductToCart']),

    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id,
        quantity: this.currentCount,
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
          this.changeProductAdded();
        });
    },

    changeProductAdded() {
      if (this.productAdded) {
        setTimeout(() => {
          this.productAdded = false;
        }, 1500);
      }
    },
  },
};
</script>

