<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image.file.url" width="120" height="120"
           alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button @click.prevent="count === 1 ? count : count--"
              type="button"
              aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>
      <label for="count">
        <input type="text" v-model.number="count" name="count">
      </label>

      <button @click.prevent="count++"
              type="button"
              aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ fullPriceForItem }} ₽
    </b>

    <button class="product__del button-del"
            type="button"
            @click.prevent="deleteProduct(item.product.id)"
            aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'CartItem',
  props: {
    item: {
      required: true,
      type: Object,
    },
  },
  methods: {
    ...mapActions(['updateCartProductCount', 'deleteCartItem']),

    deleteProduct(id) {
      this.deleteCartItem(id);
    },
  },
  computed: {
    count: {
      get() {
        return this.item.quantity;
      },

      set(value) {
        this.updateCartProductCount({ productId: this.item.product.id, quantity: value });
      },
    },

    fullPriceForItem() {
      return this.item.price * this.item.quantity;
    },
  },
};
</script>

