<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label for="price" class="form__label form__label--price">
          <input class="form__input"
                 type="number"
                 name="min-price"
                 v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label for="price" class="form__label form__label--price">
          <input class="form__input"
                 type="number"
                 name="max-price"
                 v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label for="category" class="form__label form__label--select">
          <select class="form__select" name="category" v-model="currentCategorySlug">
            <option value="">Все категории</option>
            <option v-for="category in categories"
                    :key="category.id"
                    :value="category.slug">{{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <ColorsList
        :colors="colors"
        v-model:color-id="currentColorId"
      />

      <fieldset class="form__block" v-if="currentCategorySlug === 'telefony'">
        <legend class="form__legend">Объем в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="8"
                     checked="">
              <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="16">
              <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="32">
              <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="64">
              <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox" name="volume"
                     value="128">
              <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label for="colors" class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="264">
              <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import axios from 'axios';
import { BASE_URL } from '@/api/api.config';
import ColorsList from '@/components/ColorsList/ColorsList.vue';

export default {
  components: { ColorsList },
  data() {
    return {
      currentPriceFrom: this.priceFrom,
      currentPriceTo: this.priceTo,
      currentCategorySlug: this.categorySlug,
      currentColorId: this.colorId,
      categoriesData: null,
      colorsData: null,
    };
  },
  props: ['priceFrom', 'priceTo', 'categorySlug', 'colorId'],
  name: 'ProductFilter',
  created() {
    this.loadCategories();
    this.loadColors();
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },

    colors() {
      let colors = this.colorsData ? this.colorsData.items : [];

      if (colors.length > 0) {
        colors = colors.filter((item) => !item.title.includes('<'));
      }

      return colors;
    },
  },

  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },

    priceTo(value) {
      this.currentPriceTo = value;
    },

    categorySlug(value) {
      this.currentCategorySlug = value;
    },

    colorId(value) {
      this.currentColorId = value;
    },
  },

  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categorySlug', this.currentCategorySlug);
      this.$emit('update:colorId', this.currentColorId);

      if (this.currentPriceFrom === 0 || this.currentPriceFrom === '') {
        this.deleteFilterParams('priceFrom');
      } else if (this.currentPriceFrom !== this.priceFrom) {
        this.saveFilterParams('priceFrom', this.currentPriceFrom);
      }

      if (this.currentPriceTo === 0 || this.currentPriceTo === '') {
        this.deleteFilterParams('priceTo');
      } else if (this.currentPriceTo !== this.priceTo) {
        this.saveFilterParams('priceTo', this.currentPriceTo);
      }

      if (this.currentCategorySlug === '') {
        this.deleteFilterParams('categorySlug');
      } else if (this.currentCategorySlug !== this.categorySlug) {
        this.saveFilterParams('categorySlug', this.currentCategorySlug);
      }

      if (this.currentColorId === 0) {
        this.deleteFilterParams('colorId');
      } else if (this.currentColorId !== this.colorId) {
        this.saveFilterParams('colorId', this.currentColorId);
      }
    },

    reset() {
      this.deleteFilterParams();
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:colorId', 0);
      this.$emit('update:categorySlug', '');
    },

    loadCategories() {
      axios.get(`${BASE_URL}/api/productCategories`)
        // eslint-disable-next-line no-return-assign
        .then((res) => this.categoriesData = res.data);
    },

    loadColors() {
      axios.get(`${BASE_URL}/api/colors`)
        // eslint-disable-next-line no-return-assign
        .then((res) => this.colorsData = res.data);
    },

    saveFilterParams(key, value) {
      const url = new URL(window.location);
      url.searchParams.set(key, value);
      window.history.pushState({}, '', url);
    },

    deleteFilterParams(incomeKey) {
      const url = new URL(window.location);
      const searchKeys = [];

      if (incomeKey) {
        url.searchParams.delete(incomeKey);
      } else {
        url.searchParams.forEach((value, key) => {
          searchKeys.push(key);
        });

        for (let i = 0; i < searchKeys.length; i += 1) {
          if (searchKeys[i] === 'page') {
            // eslint-disable-next-line no-continue
            continue;
          }
          url.searchParams.delete(searchKeys[i]);
        }
      }

      window.history.pushState({}, '', url);
    },
  },
};
</script>
